html, body, #root, .App {
  height: 100%;
  overflow-y: hidden; }

#body {
  height: 90%; }

footer {
  height: 10%; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40%; }

.App-header {
  background-color: #222;
  height: 20%;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

.stage {
  height: 80%;
  display: -ms-flexbox;
  display: flex;
  border: solid; }

input {
  width: 400px;
  height: 30px;
  padding-left: 10px;
  border-radius: 4px;
  border: 1px solid #bab2b2;
  -webkit-box-shadow: 0px 0px 12px #EFEFEF;
          box-shadow: 0px 0px 12px #EFEFEF; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  @media (max-width: 640px) {
    .App-header {
      font-size: xx-small; }
    input {
      width: 80%; } } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  @media (max-width: 640px) {
    .App-header {
      font-size: xx-small; }
    input {
      width: 80%; } } }



